@import url(https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC);
.App {
  padding: 1em;
}

label {
  display: inline-block;
  width: 100px;
}

header {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  font-family: 'Playfair Display SC', 'Rock Salt';
  text-align: center;
  font-size: 1.5rem;
}

/* typography */

html {
  font-family: 'helvetica neue', sans-serif;
}

thead th, tfoot th {
  font-family: 'Rock Salt';
}

th {
  letter-spacing: 2px;
  padding: 10px;
}

td {
  letter-spacing: 1px;
  overflow-x:hidden;
  overflow-y:hidden;
  font-size: 12px;
  padding: 10px;
  text-align: center;
}

/* table */

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  overflow:hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.content_td p {
  max-width: 100%;
  max-height: 100px;
  overflow-y: auto;
  text-overflow: ellipsis;
}

/* Column Size for thead */


/* Column size for tbody */

.single {
  width: 10%;
}

.double {
  width: 30%;
}

img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}

.orange {
  background-color: #fff0c7;
}

.white {
  background-color: white;
}
form[name="create"] {
  background-color: #fff0c7;
}

.input[type="text"], textarea {
  font-size: 12px;
  color: grey;
}
